* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.country--text{
  margin: 0;
  padding: 8px;
  font-size: 14px;
}

.country--item{
  display: flex;
  align-items: center;
}

.country--info--text {
  margin: 0;
    padding: 10px;
    font-size: 24px;
}

.country--name {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

span {
  font-weight: 400;
}

